.resturant-card {
    min-height: 200px;
    padding: 20px;
    border: 1px solid;
    width: 90%;
    margin: 20px 0;
    background-color: $clr3;
    img{
        height: 200px;
        width: 300px;
        object-fit: cover;
    }
    .default-img{
        width:115px;
        margin-inline-end: 105px;
    }
    .resturant-details {
        padding-inline-start: 50px;
        .resturant-title {
            font-size: 1.5rem;
        }
    }
}
@media (max-width: 700px) {
    .resturant-card{
        flex-direction: column;
        img{
            width: 100%;
        }
        .default-img{
            align-self: center;
            margin-inline-end: 0;
        }
        .resturant-details{
            padding-inline-start: 0;
            margin-top: 20px;

        }
    }
}
